import React, {useContext} from 'react';
import classes from "./NavBar.module.css";
import {Link} from "react-router-dom";
import logo from "../../../assets/logo.svg";
import {AuthContext} from "../../../context";

const NavBar = () => {
    const {isAuth, setIsAuth} = useContext(AuthContext);
    const logout = event => {
        event.preventDefault();
        setIsAuth(false);
        localStorage.removeItem('auth');
    }
    return (
        <div className={classes.navbar}>
            <img className={classes.logo} src={logo} alt=""/>
            <Link className={classes.navbar_items} to="/">Домой</Link>
            <Link className={classes.navbar_items} to="/posts">Посты</Link>
            <Link className={classes.navbar_items} to="/about">О сайте</Link>
            <Link className={classes.navbar_items} onClick={logout} to="/login">Выйти</Link>
        </div>
    );
};

export default NavBar;