import React, {useEffect, useRef, useState} from 'react';
import {usePosts} from "../hooks/usePosts";
import PostService from "../API/PostService";
import MyButton from "../components/UI/button/MyButton";
import MyModal from "../components/UI/modal/MyModal";
import PostForm from "../components/PostForm";
import PostFilter from "../components/UI/PostFilter";
import MyLoader from "../components/UI/loader/MyLoader";
import PostList from "../components/PostList";
import Pagination from "../components/UI/pagination/Pagination";
import {useFetching} from "../hooks/useFetching";
import {getPageCount} from "../components/utils/pages";
import {useObserver} from "../hooks/useObserver";
import MySelect from "../components/UI/select/MySelect";

function Posts() {
    const [posts, setPosts] = useState([]);
    const [filter, setFilter] = useState({sort: '', query: ''});
    const [modal, setModal] = useState(false);
    const sortedAndSearchedPosts = usePosts(posts, filter.sort, filter.query);
    const [totalPages, setTotalPages] = useState(0);
    const [limit, setLimit] = useState(10);
    const [page, setPage] = useState(1);
    const lastElement = useRef()

    const [fetchPosts, isPostLoading, postError] = useFetching(async (limit, page) => {
        const response = await PostService.getAll(limit, page);
        setPosts([...posts, ...response.data]);
        const totalCount = response.headers['x-total-count'];
        setTotalPages(getPageCount(totalCount, limit));
    })

    useObserver(lastElement, page <totalPages, isPostLoading, () => {
        setPage(page + 1);
    })

    useEffect(() => {
        fetchPosts(limit, page)
    }, [page, limit])

    const createPost = (newPost) => {
        setPosts([...posts, newPost]);
        setModal(false);
    }

    const removePost = (post) => {
        setPosts(posts.filter(p => p.id !== post.id));
    }

    const changePage = (page) => {
        setPage(page)
    }

    return (
        <div className="App">
            <MyButton onClick={fetchPosts}>Получить посты</MyButton>
            <MyButton style = {{marginTop: 20}} onClick = {() => setModal(true)}>
                Добавить пост
            </MyButton>
            <MyModal visible={modal} setVisible={setModal}>
                <p style={{fontFamily: 'Raleway', textAlign: 'center', color: 'white', fontSize: '40px'}}>Добавить пост</p>
                <PostForm create = {createPost}/>
            </MyModal>
            <PostFilter
                filter={filter}
                setFilter={setFilter}
            />
            <MySelect
                value={limit}
                defaultValue="Кол-во постов в списке"
                onChange={val => {setLimit(val)}}
                options={[
                    {value: 10, name: '10'},
                    {value: 15, name: '15'},
                    {value: 20, name: '20'},
                    {value: -1, name: 'Показать все'}
                ]}
            />
            {postError &&
                <h1>Произошла ошибка {postError}</h1>
            }
            <PostList remove = {removePost} posts = {sortedAndSearchedPosts} title = ""/>
            <div ref={lastElement} style={{height: '20px'}}></div>
            {isPostLoading &&
                <div style={{display: 'flex', justifyContent: 'center', marginTop: '50px'}}><MyLoader/></div>
            }
            <Pagination
                page={page}
                changePage={changePage}
                totalPages={totalPages}
            />
        </div>
    );
}

export default Posts;
