import React from 'react';
import MyButton from "./UI/button/MyButton";
import classes from "../components/UI/button/MyButton.module.css"
import {useNavigate} from "react-router-dom";

const  PostItem = (props) => {
    const navigate = useNavigate();
    return (
        <div className="post">
            <div className="post_content">
                <strong>{props.post.id}. {props.post.title}</strong>
                <div>
                    {props.post.body}
                </div>
            </div>
            <div className="post_btns">
                <MyButton onClick = {() => navigate(`/posts/${props.post.id}`)} className={classes.myBtn}>Открыть</MyButton>
                <MyButton onClick = {() => props.remove(props.post)} className={classes.myBtn}>Удалить</MyButton>
            </div>
        </div>
    );
};

export default PostItem;